import TabBar from '@/components/TabBar/index.vue'
import LeftMenu from '@/components/LeftMenu/index.vue'
import $ from 'jquery'

export default {
    data() {
        return {
            yunshiResultShow: true,
            article:{}
        }
    },
    created: function () {
        $.ajax({
            type: "GET",
            url: "/Mobile/Api/MyArtDetail",
            data: {type:8},
            dataType: "json",
            success: function(rst){
                if(rst.status==0){
                    this.article = rst.data;
                }
                else{
                    alert(rst.msg);
                }
            }
        });
    },
    components: {
        TabBar,
        LeftMenu
    },
    methods: {
    }   
}